import React, { Component, Fragment } from "react"
import { Redirect } from "react-router-dom"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { faCheck, faTimes, faEdit } from '@fortawesome/free-solid-svg-icons'
class BotoneraNuevoPedido extends Component {
    state = {
        redirect: false
    }
    cancelarPedido() {
        confirmAlert({
            title: 'Cancelar Pedido?',
            buttons: [
                {
                    label: 'Si, Cancelar',
                    onClick: async () => {
                        this.setState({
                            redirect: true
                        })
                    }
                },
                {
                    label: 'No'
                }
            ]
        });
    }
    render() {
        return (
            <div className="row">
                {this.state.redirect ?
                    <Redirect to="productos" />
                    :
                    null
                }
                <div className="col text-center">
                    <button type="button" className="btn btn-outline-light"><FontAwesomeIcon icon={faCheck} onClick={this.props.enviarPedido} /></button>
                </div>
                <div className="col text-center">
                    <button type="button" className="btn btn-outline-light"><FontAwesomeIcon icon={faTimes} onClick={e => {
                        this.cancelarPedido()
                    }} /></button>
                </div>
            </div>
        )
    }
}
export default BotoneraNuevoPedido