import React, { Component } from "react"
import "./styles/Cheque.css"
import * as firebase from "firebase"
class Cheque extends Component {
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    async enviarCheque(llave) {
        const date = new Date()
        const enviadoA = prompt("Ingresar Nombre Del Destinatario")
        if (enviadoA) {
            await firebase.database().ref().child('papeles').child(llave).update(
                {
                    ["Fecha De Salida"]: date.getDate() + "/" + (date.getMonth() + 1) + "/" + date.getFullYear(),
                    ["Enviado A"]: enviadoA
                }
            )
        }

    }
    render() {
        return (
            <div className="container cheque p-3 bg-dark">
                <div className="row d-flex align-items-center">
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Fecha Del Dia"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Entrada)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Cliente"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Cliente)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Banco"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Banco)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Numero"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Numero)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Fecha Del Cheque"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Fecha)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>$ {this.props.cheque["Monto"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Monto)</small>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    {this.props.cheque["Fecha De Salida"] ?
                                        <h6>{this.props.cheque["Fecha De Salida"]}</h6>
                                        :
                                        <h6 className="salida" onClick={
                                            e => {
                                                this.enviarCheque(this.props.llave)
                                            }
                                        }>-</h6>
                                    }
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Salida)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    {this.props.cheque["Enviado A"] ?
                                        <h6>{this.props.cheque["Enviado A"]}</h6>
                                        :
                                        <h6>-</h6>
                                    }
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Enviado A)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Cheque