import React, { Component } from "react"
import { Redirect } from "react-router-dom"
import NavBar from "../components/NavBar"
import Login from "../components/Login"
import Cheque from "../components/Cheque"
import SpinnerLoading from "../components/SpinnerLoading"
import Deuda from "../components/Deuda"
import "./Styles/ChequesList.css"

import * as firebase from "firebase"

class ChequesList extends Component {
    state = {
        filtro: "Numero",
        user: undefined,
        loading: true,
        error: null,
        redirect: false,
        cheques: []
    }
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    async fetchData() {
        const chequesRef = await firebase.database().ref().child('papeles')
        chequesRef.on("value", snapshot => {
            const arr = []
            Object.keys(snapshot.val()).map(cheque => {
                arr.push(
                    { [cheque]: snapshot.val()[cheque] }
                )
            })
            this.setState(
                {
                    cheques: arr,
                    loading: false
                }
            )
        })
    }
    authListener() {
        firebase.auth().onAuthStateChanged((user) => {
            console.log(user)
            if (user) {
                this.setState({ user })
                console.log("user added")
            }
            else {
                this.setState({
                    user: null
                })
                console.log("user quit")
            }
        })
    }
    componentDidMount() {
        this.authListener()
        this.fetchData()
    }
    handleChange(e, filtro) {
        var forEach = Array.prototype.forEach;
        var arrayCheques = document.getElementsByClassName("col-12 form-group chequeList")
        forEach.call(arrayCheques, function (f) {
            const cheque = f.children[0].children[0].children
            console.log(cheque)
            let $filtro = undefined
            switch (filtro) {
                case "Numero":
                    $filtro = 3;
                    break;
                case "Nombre":
                    $filtro = 1;
                    break;
                case "Monto":
                    $filtro = 5;
                    break;
                case "Banco":
                    $filtro = 2;
                    break;
                case "Enviado A":
                    $filtro = 7;
                    break;
                default:
                    break;
            }
            if (cheque[$filtro].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "col-12 form-group chequeList d-none";
            }
            else {
                f.className = "col-12 form-group chequeList d-block";
            }
        });
    }
    logOut() {
        firebase.auth().signOut()
    }
    actualizarBotones(e) {
        var forEach = Array.prototype.forEach;
        var arrayButton = document.getElementsByClassName("filtro btn")
        forEach.call(arrayButton, function (f) {
            f.className = "filtro btn btn-outline-light"
        })
        e.target.className = "filtro btn btn-outline-success"
    }
    render() {
        try {
            const user = this.state.user.email
            if (user === "gomezbonardi@hotmail.com") {
                if (this.state.loading) {
                    return (
                        <div>
                            <NavBar user={user} handleClick={this.logOut} />
                            <SpinnerLoading />
                        </div>
                    )
                }
                else {
                    return (
                        <div>
                            {
                                this.state.redirect ?
                                    <Redirect to="/NuevoCheque" />
                                    :
                                    <div>
                                        <NavBar user={user} handleClick={this.logOut} />
                                        <div className="productList">
                                            <div className="container">
                                                <div className="row">
                                                    <div className="col-12 text-right">
                                                        <button type="button" className="btn btn-success" onClick={
                                                            e => {
                                                                this.setState({
                                                                    redirect: true
                                                                })
                                                            }
                                                        }>Agregar Cheque</button>
                                                    </div>
                                                    <div className="col-12 text-center form-group">
                                                        <h2 className="name">Cheques</h2>
                                                    </div>
                                                    <div className="col-12  text-center form-group">
                                                        <form>
                                                            <div className="formRow">
                                                                <div className="form-group col-6 offset-3 text-center">
                                                                    <input type="text" className="form-control" onChange={
                                                                        e => {
                                                                            this.handleChange(e, this.state.filtro)
                                                                        }
                                                                    } placeholder="Buscar Cheque" />
                                                                </div>
                                                            </div>
                                                            <div className="formRow">
                                                                <div className="container">
                                                                    <div className="row">
                                                                        <div className="col text-center">
                                                                            <button type="button" className="filtro btn btn-outline-success" onClick={
                                                                                e => {
                                                                                    this.setState(
                                                                                        {
                                                                                            filtro: "Numero"
                                                                                        }
                                                                                    )
                                                                                    this.actualizarBotones(e)
                                                                                }
                                                                            }>Numero</button>
                                                                        </div>
                                                                        <div className="col text-center">
                                                                            <button type="button" className="filtro btn btn-outline-light" onClick={
                                                                                e => {
                                                                                    this.setState(
                                                                                        {
                                                                                            filtro: "Nombre"
                                                                                        }
                                                                                    )
                                                                                    this.actualizarBotones(e)
                                                                                }
                                                                            }>Nombre</button>
                                                                        </div>
                                                                        <div className="col text-center">
                                                                            <button type="button" className="filtro btn btn-outline-light" onClick={
                                                                                e => {
                                                                                    this.setState(
                                                                                        {
                                                                                            filtro: "Banco"
                                                                                        }
                                                                                    )
                                                                                    this.actualizarBotones(e)
                                                                                }
                                                                            }>Banco</button>
                                                                        </div>
                                                                        <div className="col text-center ">
                                                                            <button type="button" className="filtro btn btn-outline-light" onClick={
                                                                                e => {
                                                                                    this.setState(
                                                                                        {
                                                                                            filtro: "Monto"
                                                                                        }
                                                                                    )
                                                                                    this.actualizarBotones(e)
                                                                                }
                                                                            }>Monto</button>
                                                                        </div>
                                                                        <div className="col text-center ">
                                                                            <button type="button" className="filtro btn btn-outline-light" onClick={
                                                                                e => {
                                                                                    this.setState(
                                                                                        {
                                                                                            filtro: "Enviado A"
                                                                                        }
                                                                                    )
                                                                                    this.actualizarBotones(e)
                                                                                }
                                                                            }>Enviado A</button>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </form>
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    {Object.keys(this.state.cheques).map(nro => (
                                                        Object.keys(this.state.cheques[nro]).map(cheque => (
                                                            <div className="col-12 form-group chequeList">
                                                                <Cheque llave={cheque} cheque={this.state.cheques[nro][cheque]} />
                                                            </div>
                                                        ))
                                                    ))}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                            }
                        </div>
                    )
                }
            }
            else {
                this.logOut()
            }
        }
        catch{
            return (
                <Login redirect="Cheques" />
            )
        }
    }
}
export default ChequesList