import React, { Component, Fragment } from "react"
import { Redirect } from "react-router-dom"
import NavBar from "../components/NavBar"
import Login from "../components/Login"
import Product from "../components/Product"
import SpinnerLoading from "../components/SpinnerLoading"
import Abrazaderas from "../images/Abrazadera.png"
import AntiSalpicaduraAzul from "../images/AntiSalpicaduraAzul.png"
import AntiSalpicaduraNegro from "../images/AntiSalpicaduraNegro.png"
import AntiSalpicaduraAmarillo from "../images/AntiSalpicaduraAmarillo.png"
import AntiSalpicaduraRojo from "../images/AntiSalpicaduraRojo.png"
import AntiSalpicaduraVerde from "../images/AntiSalpicaduraVerde.png"
import Arandela from "../images/Arandela.png"
import BaldeAntiDerrame from "../images/Balde AntiDerrame.png"
import BreakAway from "../images/BreakAway.png"
import Buje3Pulg from "../images/Buje3Pulg.png"
import Buje4Pulg from "../images/Buje 4 Pulg.png"
import Flexible from "../images/Flexible.png"
import Fuelles from "../images/Fuelles.png"
import FundaAmarilla from "../images/Funda Amarilla 1 Pulg.png"
import FundaAzul from "../images/Funda Azul.png"
import FundaGris from "../images/Funda Gris.png"
import FundaNegra from "../images/Funda Negra 1 Pulg.png"
import FundaRoja from "../images/Funda Roja.png"
import FundaVerde from "../images/Funda Verde 1 Pulg.png"
import Giratorio1Pulg from "../images/Giratorio 1 Pulg.png"
import Giratorio34Pulg from "../images/Giratorio 3-4 Pulg.png"
import GomaDeFuelles from "../images/Goma De Fuelles.png"
import Junta3Fina from "../images/Junta 3 Fina.png"
import Junta4Fina from "../images/Junta 4 Fina.png"
import Junta3Gruesa from "../images/Junta 3 Gruesa.png"
import Junta4gruesa from "../images/Junta 3 Fina.png"
import Manguera1Pulg from "../images/Manguera 1 Pulg.png"
import MangueraNegra from "../images/Manguera 3-4 Pulg Negra.png"
import MangueraRoja from "../images/Manguera 3-4 Pulg Roja.png"
import MangueraVerde from "../images/Manguera 3-4 Pulg Verde.png"
import PastaDeAgua from "../images/Pasta De Agua.png"
import Pico11A from "../images/Pico 11-A.png"
import Pico120L from "../images/Pico 120-L.png"
import RepuestoDeTapa from "../images/Repuesto De Tapa.png"
import Sombrerete2Pulg from "../images/Sombrerete 2 Pulg.png"
import Tapa3Pulg from "../images/Tapa 3 Pulg.png"
import Tapa4Pulg from "../images/Tapa 4 Pulg.png"
import TapaExpansora from "../images/Tapa Expansora.png"
import ValvulaBajoSurtidor from "../images/Valvula Bajo Surtidor.png"
import ValvulaDePie from "../images/Valvula De Pie.png"


import * as firebase from "firebase"
import "./Styles/ProductList.css"

class ProductList extends Component {
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    async fetchData() {
        const productosRef = await firebase.database().ref().child('productos2')
        await productosRef.on("value", snapshot => {
            this.setState({
                products: snapshot.val()
            })
        })
        const mecanizadosRef = await firebase.database().ref().child('mecanizados2')
        await mecanizadosRef.on("value", snapshot => {
            this.setState({
                mecanizados: snapshot.val()
            })
        })
        const noMecanizadosRef = await firebase.database().ref().child('no-mecanizados2')
        await noMecanizadosRef.on("value", snapshot => {
            this.setState({
                noMecanizados: snapshot.val(),
                loading: false
            })
        })

    }
    componentDidMount() {
        this.fetchData()
        this.authListener()
    }

    authListener() {
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                this.setState({ user })
            }
            else {
                this.setState({
                    user: null
                })
            }
        })
    }
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var array = document.getele
        var arrayProductos = document.getElementsByClassName("col-6 col-md-3 col-lg-2 form-group text-center align-items-center")
        forEach.call(arrayProductos, function (f) {
            if (f.children[0].children[1].children[0].children[0].children[0].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "col-6 col-md-3 col-lg-2 form-group text-center d-none align-items-center"
            }
            else {
                f.className = "col-6 col-md-3 col-lg-2 form-group text-center d-flex align-items-center"
            }
        });
    }
    state = {
        search: undefined,
        user: undefined,
        loading: true,
        error: null,
        products: [
        ],
        mecanizados: [

        ],
        noMecanizados: [

        ],
        images: {
            "Abrazaderas": Abrazaderas,
            "AntiSalpicadura Amarillo": AntiSalpicaduraAmarillo,
            "AntiSalpicadura Azul": AntiSalpicaduraAzul,
            "AntiSalpicadura Negro": AntiSalpicaduraNegro,
            "AntiSalpicadura Rojo": AntiSalpicaduraRojo,
            "AntiSalpicadura Verde": AntiSalpicaduraVerde,
            "Arandela": Arandela,
            "Balde AntiDerrame": BaldeAntiDerrame,
            "Break Away 1 Pulg": BreakAway,
            "Break Away 3-4 Pulg": BreakAway,
            "Buje 3 Pulg": Buje3Pulg,
            "Buje 4 Pulg": Buje4Pulg,
            "Flexible": Flexible,
            "Fuelles": Fuelles,
            "Funda Amarilla": FundaAmarilla,
            "Funda Amarilla 1 Pulg": FundaAmarilla,
            "Funda Azul": FundaAzul,
            "Funda Gris": FundaGris,
            "Funda Negra": FundaNegra,
            "Funda Negra 1 Pulg": FundaNegra,
            "Funda Roja": FundaRoja,
            "Funda Verde": FundaVerde,
            "Funda Verde 1 Pulg": FundaVerde,
            "Giratorio 1 Pulg": Giratorio1Pulg,
            "Giratorio 3-4 Pulg": Giratorio34Pulg,
            "Goma De Fuelles": GomaDeFuelles,
            "Junta 3 Fina": Junta3Fina,
            "Junta 3 Gruesa": Junta3Gruesa,
            "Junta 4 Fina": Junta4Fina,
            "Junta 4 Gruesa": Junta4gruesa,
            "Manguera 1 Pulg": Manguera1Pulg,
            "Manguera 5-8 Pulg Roja": MangueraRoja,
            "Manguera 5-8 Pulg Negra": MangueraNegra,
            "Manguera 5-8 Pulg Verde": MangueraVerde,
            "Pasta De Agua": PastaDeAgua,
            "Pico 11-A": Pico11A,
            "Pico 120-L": Pico120L,
            "Repuesto De Tapa": RepuestoDeTapa,
            "Sombrerete 2 Pulg": Sombrerete2Pulg,
            "Tapa 3 Pulg": Tapa3Pulg,
            "Tapa 4 Pulg": Tapa4Pulg,
            "Tapa Expansora": TapaExpansora,
            "Valvula Bajo Surtidor": ValvulaBajoSurtidor,
            "Valvula Bajo Surtidor 2 Pulg": ValvulaBajoSurtidor,
            "Valvula De Pie": ValvulaDePie,
            "Valvula De Pie 2 Pulg": ValvulaDePie,
            "Valvula De Pie 2 Pulg 1-2": ValvulaDePie,
            "Valvula De Pie 3 Pulg": ValvulaDePie,
            "Valvula De Pie 4 Pulg": ValvulaDePie,
        },

    }
    logOut() {
        firebase.auth().signOut()
    }
    render() {
        try {
            let user
            user = this.state.user.email
            if (user) {
                if (this.state.loading) {
                    return (
                        <Fragment>
                            <NavBar user={user} handleClick={this.logOut} />
                            <SpinnerLoading />
                        </Fragment>
                    )
                }
                else {
                    return (
                        <div>
                            <NavBar user={user} handleClick={this.logOut} />
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChange(e)
                                                            }
                                                        } placeholder="Buscar Producto" />
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.values(this.state.products).map(producto => (
                                            <div className="col-6 col-md-3 col-lg-2 form-group text-center d-flex align-items-center" key={producto.nombre}>
                                                <Product nombre={producto.nombre} cantidad={producto.cantidad} imagen={this.state.images[producto.nombre]} seccion="productos" mecanizados={this.state.mecanizados} noMecanizados={this.state.noMecanizados} />
                                            </div>
                                        ))
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }
            }
        }
        catch (error) {
            return (
                <Login redirect="productos" />
            )
        }
    }
}
export default ProductList