import React, { Component } from "react"
import PedidoItem from "./PedidoItem"
import BotoneraPedido from "./BotoneraPedido"
import "./styles/Pedido.css"
import * as firebase from "firebase"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { parse } from "@babel/core";

class Pedido extends Component {
    state = {
        pedido: [],

    }
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    componentDidMount() {
        this.consultarStockActual(this.props.pedido.articulos)
    }
    async restarProducto(producto, nuevaCantidad, seccion) {
        console.log(producto, nuevaCantidad, seccion)
        const productoRef = await firebase.database().ref().child(seccion + "2").child(producto)
        productoRef.update(
            {
                cantidad: nuevaCantidad
            }
        )

    }
    async consultarStockActual(pedido) {
        const arr = this.state.pedido
        Object.keys(pedido).map(async articulo => {
            console.log(pedido[articulo])
            if (pedido[articulo].nombre === "Evertite 3 Pulg") {
                await firebase.database().ref().child('productos2').child("Tapa 3 Pulg").child("cantidad").on("value", snapshot => {
                    this.setState({
                        "Tapa 3 Pulg": snapshot.val()
                    })
                })
                await firebase.database().ref().child('productos2').child("Buje 3 Pulg").child("cantidad").on("value", snapshot => {
                    this.setState({
                        "Buje 3 Pulg": snapshot.val()
                    })
                })
            }
            else if (pedido[articulo].nombre === "Evertite 4 Pulg") {
                await firebase.database().ref().child('productos2').child("Tapa 4 Pulg").child("cantidad").on("value", snapshot => {
                    this.setState({
                        "Tapa 4 Pulg": snapshot.val()
                    })
                })
                await firebase.database().ref().child('productos2').child("Buje 4 Pulg").child("cantidad").on("value", snapshot => {
                    this.setState({
                        "Buje 4 Pulg": snapshot.val()
                    })
                })
            }
            if (pedido[articulo].seccion == "Surtidores") {
                await firebase.database().ref().child('Surtidores2').child(pedido[articulo].nombre).child("cantidad").on("value", snapshot => {
                    arr.push({
                        nombre: pedido[articulo].nombre,
                        cantidad: snapshot.val()
                    })
                })

            }
            await firebase.database().ref().child('productos2').child(pedido[articulo].nombre).child("cantidad").on("value", snapshot => {
                arr.push({
                    nombre: pedido[articulo].nombre,
                    cantidad: snapshot.val()

                })
            })
            this.setState({
                pedido: arr
            })
        })
    }
    async descontarPedido(pedido) {
        Object.keys(pedido).map(async articulo => {
            console.log(pedido[articulo].seccion)
            if (pedido[articulo].nombre === "Evertite 3 Pulg") {
                this.restarProducto("Tapa 3 Pulg", this.state["Tapa 3 Pulg"] - parseInt(pedido[articulo].cantidad))
                this.restarProducto("Buje 3 Pulg", this.state["Buje 3 Pulg"] - parseInt(pedido[articulo].cantidad))
            }
            if (pedido[articulo].nombre === "Evertite 4 Pulg") {
                this.restarProducto("Tapa 4 Pulg", this.state["Tapa 4 Pulg"] - parseInt(pedido[articulo].cantidad))
                this.restarProducto("Buje 4 Pulg", this.state["Buje 4 Pulg"] - parseInt(pedido[articulo].cantidad))
            }
            else {
                this.restarProducto(this.state.pedido[articulo].nombre, this.state.pedido[articulo].cantidad - parseInt(pedido[articulo].cantidad), pedido[articulo].seccion)
            }
        })
    }
    render() {
        return (
            <div className="pedido bg-dark p-4">
                <div className="container">
                    <div className="row text-center">
                        <div className="col-12 mb-2">
                            <h4><span className="badge-pill badge-dark p-2">{this.props.cliente}</span></h4>
                        </div>
                        <div className="col-12">
                            {Object.keys(this.props.pedido.articulos).map(articulo => (
                                <PedidoItem nombre={this.props.pedido.articulos[articulo].nombre} cantidad={this.props.pedido.articulos[articulo].cantidad} />
                            ))}
                        </div>
                    </div>
                    <BotoneraPedido enviarPedido={async e => {
                        confirmAlert({
                            title: 'Enviar Pedido?',
                            buttons: [
                                {
                                    label: 'Enviar',
                                    onClick: async () => {
                                        await this.descontarPedido(this.props.pedido.articulos)
                                        this.props.enviarPedido(this.props.id, this.props.pedido)
                                    }
                                },
                                {
                                    label: 'Cancelar'
                                }
                            ]
                        });

                    }}
                        eliminarPedido={e => {
                            confirmAlert({
                                title: 'Eliminar Pedido?',
                                buttons: [
                                    {
                                        label: 'Eliminar',
                                        onClick: async () => {
                                            this.props.eliminarPedido(this.props.id)
                                        }
                                    },
                                    {
                                        label: 'Cancelar'
                                    }
                                ]
                            });
                        }} />
                </div>
            </div>
        )
    }
}
export default Pedido